nav{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 0.5em;
}

.profile-wrapper{
    font-size: 2.5em;
    padding: 0.1em 0.3em;
}

.profile-icon{
    cursor: pointer;
}

.link-wrapper{
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    gap: 30px;
    font-size: 1.7em;
}

.link-item h4{
    cursor: pointer;
}